import { useState, useEffect, Fragment } from 'react';
import { useParams } from "react-router-dom";
import EventoWrapper from "../components/eventos/EventoWrapper";
import { doc, getFirestore, getDoc } from "firebase/firestore";
import MyFirebase from "../database/firebase";
import Loader from '../components/UI/Loader';
import useUser from '../hooks/use-user';
import MisContactos from '../components/profile/MisContactos';
import { BsWhatsapp } from "react-icons/bs";
import AddContacto from '../components/profile/AddContacto';
import { getAuth, signOut } from "firebase/auth";
import { useNavigate } from "react-router-dom";
const app = MyFirebase();

const ProfilePage = props => {
    const params = useParams();
    const href = window.location.href;
    const profileId = params.userId;
    const user = useUser(profileId)
    const [eventos, setEventos] = useState();
    const navigate = useNavigate();
    const [isLoading, setIsloading] = useState(true)
    const db = getFirestore(app);
    const [isMisEventos, setIsMisEventos] = useState(true);
    const itsMe = false;
    const resultHandler = () => {
        (isMisEventos) ? setIsMisEventos(false) : setIsMisEventos(true)
    }

    useEffect(() => {
        const loadedEventos = [];
        if (!!user.createdEventos) {
            user.createdEventos.forEach((createdEvento) => {
                const eventoRef = doc(db, 'eventos', createdEvento);
                getDoc(eventoRef).then((doc) => {
                    loadedEventos.push(doc.data());
                })
            })
           
        }
        setEventos(loadedEventos)
        setTimeout(()=>{setIsloading(false)},700)
        
    }, [user, db])

    const logoutHandler = () => {
        const auth = getAuth();
        signOut(auth).then(() => {
          localStorage.removeItem('uid');
          navigate('/register')
        }).catch((error) => {
          // An error happened.
        });
    }

    return (
        <Fragment>
             {isLoading
              ? <div className="loader-wrapper"><Loader /></div>
              :<section className="gapper padding">
                    <div className="profile-header gapper center">
                        <div className="profile-avatar">{(user.displayName.trim().indexOf(' ') !== -1)  ? user.displayName.trim().split(" ").map((n)=>n[0]).join("") : user.displayName.trim().substring(0,2)}</div>
                        <h1>{user.displayName.toUpperCase()}</h1>
                    </div>
                    {itsMe && <a target='_blank' className='compartir-id' rel='noreferrer' title='share' href={`https://api.whatsapp.com/send?text=Añádeme%20a%20Ergonomic!%20/${href}`}><BsWhatsapp />COMPARTIR MI PERFIL</a>}
                    {itsMe &&  <button className='cerrar-sesion' onClick={logoutHandler}>CERRAR SESION</button>}
                    {itsMe && <div className="profile-body gapper">
                        <div className="profile-body-selector">
                            <button className={(isMisEventos) ? 'selected' : ''} onClick={resultHandler}>MIS EVENTOS</button>
                            <button className={(!isMisEventos) ? 'selected' : ''} onClick={resultHandler}>MIS CONTACTOS</button>
                        </div>
                        <div className='profile-body-result'>
                            {isMisEventos
                            ?<EventoWrapper eventos={eventos}/>
                            :<MisContactos contactos={user.engagedTo}/>
                            }
                        </div>
                    </div>    
                    }
                    {!itsMe && <AddContacto />}
                </section>
            }
        </Fragment>
        
    )
}

export default ProfilePage;